<template>
  <div class="loader" v-if="loading"></div>

  <div class="container mt-3 mb-5" v-else>
    <section
      class="hotel-image"
      v-bind:style="{
        backgroundImage: 'url(' + image + hotel.h_image + ')',
      }"
    ></section>
    <div class="hotel-name-sec mt-4 mb-4">
      <h4 class="hotel-name bold text-4 lg:text-6">{{ hotel.h_name }}</h4>
      <p>
        <span class="fa fa-star mt-2 checked"></span> {{ hotel.h_stars }} ستاره
        |
        <span class="fs-9 text-muted loc-place">{{ hotel.h_address }}</span>
      </p>
    </div>
    <hr />
    <div class="row mt-4 main-hotel">
      <div class="col-md-8">
        <div class="hotel-infos">
          <h4 class="bold orange-font">مشخصات :</h4>
          <p class="prim-font">
            <i class="bi bi-tag-fill"></i> &nbsp;دسته : {{ hotel.city }}/{{
              hotel.name
            }}
          </p>
          <p class="prim-font">
            <i class="bi bi-compass"></i>&nbsp; آدرس : {{ hotel.h_address }}
          </p>
          <p></p>
          <p class="prim-font">
            <i class="bi bi-hand-index-fill"></i>
            &nbsp; قوانین : {{ hotel.h_roles }}
          </p>
          <h4 class="bold orange-font mt-3">توضیحات :</h4>

          <p class="hotel-dec">
            {{ hotel.h_body }}
          </p>
        </div>
      </div>
      <div class="col-md-4">
        <div class="hotel-infos sticky-top">
          <h4 class="bold orange-font">اطلاعات :</h4>
          <p class="mt-3 prim-font">
            <i class="bi bi-arrow-return-left"></i>
            &nbsp;ساعت ورود : {{ hotel.h_in }}
          </p>
          <p class="mt-3 prim-font">
            <i class="bi bi-arrow-return-right"></i>
            &nbsp;ساعت خروج : {{ hotel.h_out }}
          </p>
          <p class="mt-3 prim-font">
            <i class="bi bi-people-fill"></i>
            &nbsp;ظرفیت : {{ hotel.h_capacity }} نفر
          </p>
          <p class="mt-3 prim-font">
            <i class="bi bi-emoji-laughing-fill"></i>
            &nbsp;سطح کیفیت :
            <span
              class="fa fa-star"
              v-for="index in hotel.h_stars"
              :key="index"
            ></span>
          </p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { ref, reactive } from "vue";
import { useRoute } from "vue-router";
import { useMeta } from "vue-meta";
export default {
  name: "home",

  setup() {
    const hotel = ref();
    const loading = ref(true);
    const route = useRoute();
    const image = process.env.VUE_APP_IMAGES_URL;
    axios
      .get(`${process.env.VUE_APP_API_URL}show-hotel-by-id/${route.params.id}`)
      .then(function (response) {
        hotel.value = response.data.data.hotel;
        loading.value = false;
      })
      .catch(function (error) {
        console.log(error);
      });

    useMeta({
      title: process.env.VUE_APP_NAME + " | " + route.params.name,
      htmlAttrs: { lang: "FA", amp: true },
      description:
        " اجاره ویلا و اقامتگاه , رزرو هتل های داخلی و خارجی  رزرو هتل , سوییت , اجازه خانه  همراه با ضمانت همیار سفر",
    });

    return { hotel, image, loading };
  },
};
</script>

