import { createRouter, createWebHistory } from 'vue-router';
import axios from "axios";
const getCookie = (cookie_name) => {
    const re = new RegExp(`(?<=${cookie_name}=)[^;]*`);
    try {
        return document.cookie.match(re)[0]; // Will raise TypeError if cookie is not found
    } catch {
        return "this-cookie-doesn't-exist";
    }
};
let token = getCookie("Token");
const routes = [
    //front

    {
        path: '/',
        component: require('../layout/front.vue').default,
        children: [
            {
                path: '',
                component: require('../pages/home/index.vue').default,
                name: 'home'
            },
            {
                path: 'hotel/:id/:name',
                component: require('../pages/home/post.vue').default,
                name: 'hotel'
            },
            {
                path: '/:pathMatch(.*)*', component: require('../pages/error/404.vue').default, name: '404'
            }
        ]
    },
    //auth
    {
        path: '/login',
        component: require('../pages/auth/login.vue').default,
        name: 'login'
    },
    {
        path: '/confirm-login',
        component: require('../pages/auth/confirm.vue').default,
        name: 'confirm'
    },



    {
        path: '/admin',
        name: 'admin',
        meta: {
            admin: true
        },
        component: require('../layout/admin.vue').default,
        children: [
            {
                path: '',
                component: require('../pages/admin/home').default,
                alias: '/site-admin',
                name: 'admin'
            },
            {
                path: 'hotels',
                component: require('../pages/admin/hotels/index').default,
                name: 'hotels'
            },
            {
                path: 'category',
                component: require('../pages/admin/category/index').default,
                name: 'category'
            },
            {
                path: 'users',
                component: require('../pages/admin/users/index').default,
                name: 'users'
            },

        ]
    },


]
const router = createRouter({
    history: createWebHistory(),
    routes,
});



router.beforeEach((routeto, routefrom, next) => {
    if (routeto.meta.admin) {
        axios
            .get(`${process.env.VUE_APP_API_URL}role-check`, {
                headers: {
                    Authorization: `Bearer ${token}`,
                },
            })
            .then(function (response) {
                if (routeto.meta.admin && response.data.admin == false) {
                    next('/')
                }
                next()

            })
            .catch(function (error) {
                console.log(error)
                if (routeto.meta.admin && error) {
                    next('/')
                }
                next()

            });
    } else {
        next()
    }
})


export default router;
