<template>
  <div class="container auth">
    <div class="row mx-0">
      <div class="col-md-6 px-0 d-none d-sm-block">
        <div class="auth-background"></div>
      </div>
      <div class="col-md-6 px-0">
        <div class="auth-side">
          <h2 class="text-center bold mt-5" style="color: #155263">
            ورود به سایت
          </h2>
          <p class="auth-info mt-4">
            برای استفاده از خدمات باشگاه سایت و مدیریت حساب کاربری خود وارد سایت
            شوید.
          </p>
          <form class="mt-4" @submit.prevent="validate">
            <input
              class="form-control auth-inputs"
              placeholder="ایمیل"
              name="email"
              type="email"
              v-model="form.email"
            />
            <p class="color-danger text-danger text-center mt-3 fw-bold">
              {{ form.emailErrorText }}
            </p>
            <center>
              <button class="btn auth-submit mt-1" v-if="!loading">ورود</button>
              <button class="btn auth-submit mt-1" disabled v-else>
                درحال بررسی ...
              </button>
              <router-link style="text-decoration: none; color: #155263" to="/">
                <p class="mt-4 bold">بازگشت به صفحه اصلی</p>
              </router-link>
            </center>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { reactive, ref } from "vue";
import axios from "axios";
import router from "../../router/routes";
import Swal from "sweetalert2";

export default {
  name: "login",
  setup() {
    const loading = ref(false);
    const form = reactive({
      email: "",
      emailErrorText: "",
    });

    function validate() {
      if (form.email === "") {
        form.emailErrorText = "فیلد ایمیل را پر کنید";
      } else {
        form.emailErrorText = "";
        login();
      }
    }

    function login() {
      loading.value = true;
      axios
        .post(`${process.env.VUE_APP_API_URL}login`, {
          email: form.email,
        })
        .then(function (response) {
          response;
          loading.value = false;
          router.push({ path: "confirm-login", query: { email: form.email } });
        })
        .catch(function (error) {
          loading.value = false;
          console.log(error)
          Swal.fire({
            icon: "error",
            title: error.response.data.message.message,
            toast: true,
            position: "top",
            showConfirmButton: false,
            timer: 4000,
            timerProgressBar: true,
          });
        });
    }

    return { validate, form, loading };
  },
};
</script>

