<template>
  <div class="container mb-5">
    <section class="intro-sec">
      <h1 class="bold">جستجوکنید , مسافرت کنید</h1>
      <h1 class="bold">لذت ببرید</h1>
      <p class="text-intro mt-4">معرفی و بررسی هتل های کشور</p>
      <p class="text-intro">در همیار سفر</p>
      <div class="filter-hotel">
        <div class="row">
          <div class="col-md-3">
            <div class="row">
              <div class="input-group">
                <span class="input-group-text" id="basic-addon1">
                  <i class="bi bi-geo-fill"></i>
                </span>
                <select
                  class="form-select filter-input"
                  v-model="form.category"
                >
                  <option value>انتخاب کنید</option>
                  <option
                    v-for="category in categories"
                    :key="category.id"
                    :value="category.c_id"
                  >
                    {{ category.name }} / {{ category.city }}
                  </option>
                </select>
              </div>
            </div>
          </div>
          <div class="col-md-3">
            <div class="row">
              <div class="input-group">
                <span class="input-group-text" id="basic-addon1">
                  <i class="bi bi-star-fill"></i>
                </span>
                <select class="form-select filter-input" v-model="form.stars">
                  <option value>انتخاب کنید</option>
                  <option value="1">1 ستاره</option>
                  <option value="2">2 ستاره</option>
                  <option value="3">3 ستاره</option>
                  <option value="4">4 ستاره</option>
                  <option value="5">5 ستاره</option>
                </select>
              </div>
            </div>
          </div>

          <div class="col-md-3">
            <div class="input-group">
              <span class="input-group-text" id="basic-addon1">
                <i class="bi bi-person-fill"></i>
              </span>
              <select class="form-select filter-input" v-model="form.capacity">
                <option value>انتخاب کنید</option>
                <option value="1">یک نفر</option>
                <option value="2">دو نفر</option>
                <option value="3">سه نفر</option>
                <option value="4">چهار نفر</option>
                <option value="6">پنج نفر</option>
              </select>
            </div>
          </div>
          <div class="col-md-3">
            <button
              class="btn filter-btn"
              @click="search()"
              v-if="!filterloading"
            >
              فیلتر
            </button>
            <button class="btn filter-btn" disabled v-if="filterloading">
              درحال جستوجو ....
            </button>
          </div>
        </div>
      </div>
    </section>
  </div>

  <div class="mymodal" style="display: block" v-if="searchmodal">
    <div class="modal-dialog-search" style="margin-top: 150px">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title">نتایج جستوجو</h5>
          <button
            type="button"
            class="btn-close"
            data-bs-dismiss="modal"
            aria-label="Close"
            @click="searchmodal = !searchmodal"
          ></button>
        </div>
        <div v-if="hotel.length < 1" class="text-center">
          <img
            src="./undraw_Notify_re_65on.png"
            class="img-thumbnail notfound mt-5"
          />
          <h2 style="color: #ff6f3c" class="bold mb-5">
            هتلی با این مشخصات یافت نشد
          </h2>
        </div>
        <div v-else>
          <p class="m-5 fs-5" v-for="hotels in hotel" :key="hotels.id">
            <router-link
              class="link-none"
              :to="{
                name: 'hotel',
                params: { id: hotels.id, name: hotels.h_name },
              }"
            >
              {{ hotels.h_name }}
            </router-link>
          </p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { ref, reactive } from "vue";
export default {
  setup() {
    const form = reactive({
      category: "",
      stars: "",
      capacity: "",
    });
    const categories = ref({});
    const hotel = ref({});
    const filterloading = ref(false);
    const searchmodal = ref(false);
    function category() {
      axios
        .get(`${process.env.VUE_APP_API_URL}show-categories`)
        .then(function (response) {
          categories.value = response.data.data.categories;
        })
        .catch(function (error) {
          console.log(error);
        });
    }
    category();
    function search() {
      filterloading.value = true;
      axios
        .post(`${process.env.VUE_APP_API_URL}filter-hotel`, {
          category: form.category,
          stars: form.stars,
          cap: form.capacity,
        })
        .then(function (response) {
          searchmodal.value = true;
          filterloading.value = false;
          hotel.value = response.data.data.hotel;
          response;
        })
        .catch(function (error) {
          error;
        });
    }
    return { form, categories, search, searchmodal, hotel, filterloading };
  },
  name: "intro",
};
</script>

<style>
</style>