<template>
  <div class="container mt-3">
    <div class="row">
      <div class="col-md-6">
        <h5 class="fw-bold">هتل های ثبت شده</h5>
      </div>
      <div class="col-md-6">
        <button
          type="button"
          style="float: left"
          class="btn btn-primary"
          data-bs-toggle="modal"
          data-bs-target="#exampleModal"
        >
          افزودن
        </button>
      </div>
    </div>

    <div v-if="postloading" class="loading">
      <div class="spinner-grow text-primary" role="status"></div>
      <h4 class="mt-3 fw-bold">درحال بارگزاری</h4>
    </div>

    <div class="table" v-else>
      <div v-if="showpost.length < 1" style="margin-top: 100px">
        <h3 class="text-center fw-bold text-primary">هتلی یافت نشد !</h3>
      </div>
      <table class="table mt-5" v-else>
        <thead class="table-primary">
          <tr>
            <th scope="col">شناسه</th>
            <th scope="col">نام</th>
            <th scope="col">دسته بندی</th>
            <th scope="col">ستاره</th>
            <th scope="col">تصویر</th>
            <th scope="col">عملیات</th>
          </tr>
        </thead>
        <tbody class="list">
          <tr v-for="post in showpost" :key="post.id">
            <th scope="row">{{ post.id }}</th>
            <td>
              <p class="fw-bold">{{ post.h_name }}</p>
            </td>
            <td>
              <p class="fw-bold">{{ post.name }} / {{ post.city }}</p>
            </td>
            <td>
              <span
                class="fa fa-star"
                v-bind:class="{ checked: post.h_stars > 0 }"
              ></span>
              <span
                class="fa fa-star"
                v-bind:class="{ checked: post.h_stars > 1 }"
              ></span>
              <span
                class="fa fa-star"
                v-bind:class="{ checked: post.h_stars > 2 }"
              ></span>
              <span
                class="fa fa-star"
                v-bind:class="{ checked: post.h_stars > 3 }"
              ></span>
              <span
                class="fa fa-star"
                v-bind:class="{ checked: post.h_stars > 4 }"
              ></span>
            </td>
            <td>
              <img
                v-bind:src="'http://localhost:8000/' + 'images/' + post.h_image"
                class="image-list"
              />
            </td>
            <td>
              <div class="dropdown">
                <button
                  class="btn btn-light-primary dropdown-toggle"
                  type="button"
                  id="dropdownMenuButton1"
                  data-bs-toggle="dropdown"
                  aria-expanded="false"
                >
                  عملیات
                </button>
                <ul class="dropdown-menu" aria-labelledby="dropdownMenuButton1">
                  <li><a class="dropdown-item" href="#">نمایش</a></li>
                  <li>
                    <a class="dropdown-item" @click="edite(post.id)" href="#"
                      >ویرایش</a
                    >
                  </li>
                  <li>
                    <a
                      class="dropdown-item"
                      href="#"
                      @click="deletepost(post.id)"
                      >حذف</a
                    >
                  </li>
                </ul>
              </div>
            </td>
          </tr>
        </tbody>
      </table>
    </div>

    <!-- Modal -->
    <div
      class="modal fade mt-5"
      id="exampleModal"
      tabindex="-1"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="exampleModalLabel">افزودن مطلب</h5>
            <button
              type="button"
              class="btn-close"
              data-bs-dismiss="modal"
              aria-label="Close"
            ></button>
          </div>
          <form>
            <div class="modal-body">
              <div class="row gy-4">
                <div class="col-md-6">
                  <label class="form-label">نام هتل</label
                  ><input
                    class="form-control"
                    type="text"
                    v-model="form.name"
                  />
                  <p class="color-danger text-muted mt-2">
                    {{ form.errorname }}
                  </p>
                </div>
                <div class="col-md-6">
                  <label class="form-label">دسته بندی</label
                  ><select class="form-control" v-model="form.category">
                    <option
                      :value="cat.c_id"
                      v-for="cat in categories"
                      :key="cat.c_id"
                    >
                      {{ cat.name }} / {{ cat.city }}
                    </option>
                  </select>
                </div>

                <div class="col-md-6">
                  <label class="form-label">تعداد ستاره</label
                  ><select class="form-control" v-model="form.stars">
                    <option value="1">1</option>
                    <option value="2">2</option>
                    <option value="3">3</option>
                    <option value="4">4</option>
                    <option value="5">5</option>
                  </select>
                </div>

                <div class="col-md-6">
                  <label class="form-label">نفرات قابل پذیرایی</label
                  ><select class="form-control" v-model="form.capacity">
                    <option value="1">1 نفر</option>
                    <option value="2">2 نفر</option>
                    <option value="3">3 نفر</option>
                    <option value="4">4 نفر</option>
                    <option value="5">5 نفر</option>
                  </select>
                </div>

                <div class="col-md-6">
                  <label class="form-label">ساعت ورود</label>
                  <input type="time" class="form-control" v-model="form.in" />
                  {{ form.errorin }}
                </div>

                <div class="col-md-6">
                  <label class="form-label">ساعت خروج</label>
                  <input type="time" class="form-control" v-model="form.out" />
                  {{ form.errorout }}
                </div>

                <div class="col-md-6">
                  <label class="form-label">تصویر</label
                  ><input
                    class="form-control"
                    type="file"
                    @change="uploadImage"
                    name="image"
                    id="image"
                    accept="image/*"
                  />
                  <p class="color-danger red mt-2">
                    {{ form.imageMessage }}
                  </p>
                </div>

                <div class="col-md-12">
                  <label class="form-label">قوانین</label
                  ><input
                    class="form-control"
                    type="text"
                    v-model="form.roles"
                  />
                  <p class="color-danger red mt-2">
                    {{ form.errorroles }}
                  </p>
                </div>

                <div class="col-md-12">
                  <label class="form-label">آدرس</label
                  ><input
                    class="form-control"
                    type="text"
                    v-model="form.address"
                  />
                  <p class="color-danger red mt-2">
                    {{ form.erroraddress }}
                  </p>
                </div>

                <div class="col-md-12">
                  <label class="form-label">متن نوشته</label
                  ><textarea
                    class="form-control"
                    rows="2"
                    v-model="form.body"
                  ></textarea>
                  <p class="color-danger text-muted mt-2">
                    {{ form.bodyMessage }}
                  </p>
                </div>
              </div>
            </div>
            <div class="modal-footer">
              <button
                type="button"
                class="btn btn-secondary"
                data-bs-dismiss="modal"
              >
                بستن
              </button>
              <button type="button" class="btn btn-primary" @click="validate()">
                انتشار
              </button>
            </div>
          </form>
        </div>
      </div>
    </div>

    <div class="mymodal" style="display: block" v-if="modal">
      <div class="modal-dialog" style="margin-top: 100px" v-if="!editloading">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title">ویرایش مطلب</h5>
            <button
              type="button"
              class="btn-close"
              data-bs-dismiss="modal"
              aria-label="Close"
              @click="(modal = !modal), clear()"
            ></button>
          </div>
          <form>
            <div class="modal-body">
              <div class="row gy-4">
                <div class="col-md-6">
                  <label class="form-label">نام هتل</label
                  ><input
                    class="form-control"
                    type="text"
                    v-model="form.name"
                  />
                  <p class="color-danger text-muted mt-2">
                    {{ form.errorname }}
                  </p>
                </div>
                <div class="col-md-6">
                  <label class="form-label">دسته بندی</label>
                  <select class="form-control" v-model="form.category">
                    <option
                      :value="cat.c_id"
                      v-for="cat in categories"
                      :key="cat.c_id"
                    >
                      {{ cat.name }} / {{ cat.city }}
                    </option>
                  </select>
                </div>

                <div class="col-md-6">
                  <label class="form-label">تعداد ستاره</label
                  ><select class="form-control" v-model="form.stars">
                    <option value="1">1</option>
                    <option value="2">2</option>
                    <option value="3">3</option>
                    <option value="4">4</option>
                    <option value="5">5</option>
                  </select>
                </div>

                <div class="col-md-6">
                  <label class="form-label">نفرات قابل پذیرایی</label
                  ><select class="form-control" v-model="form.capacity">
                    <option value="1">1 نفر</option>
                    <option value="2">2 نفر</option>
                    <option value="3">3 نفر</option>
                    <option value="4">4 نفر</option>
                    <option value="5">5 نفر</option>
                  </select>
                </div>

                <div class="col-md-6">
                  <label class="form-label">ساعت ورود</label>
                  <input type="time" class="form-control" v-model="form.in" />
                  {{ form.errorin }}
                </div>

                <div class="col-md-6">
                  <label class="form-label">ساعت خروج</label>
                  <input type="time" class="form-control" v-model="form.out" />
                  {{ form.errorout }}
                </div>

                <div class="col-md-6">
                  <label class="form-label">تصویر</label
                  ><input
                    class="form-control"
                    type="file"
                    @change="uploadImage"
                    name="image"
                    id="image"
                    accept="image/*"
                  />
                  <p class="color-danger red mt-2">
                    {{ form.imageMessage }}
                  </p>
                </div>

                <div class="col-md-12">
                  <label class="form-label">قوانین</label
                  ><input
                    class="form-control"
                    type="text"
                    v-model="form.roles"
                  />
                  <p class="color-danger red mt-2">
                    {{ form.errorroles }}
                  </p>
                </div>

                <div class="col-md-12">
                  <label class="form-label">آدرس</label
                  ><input
                    class="form-control"
                    type="text"
                    v-model="form.address"
                  />
                  <p class="color-danger red mt-2">
                    {{ form.erroraddress }}
                  </p>
                </div>

                <div class="col-md-12">
                  <label class="form-label">متن نوشته</label
                  ><textarea
                    class="form-control"
                    rows="2"
                    v-model="form.body"
                  ></textarea>
                  <p class="color-danger text-muted mt-2">
                    {{ form.bodyMessage }}
                  </p>
                </div>
              </div>
            </div>
            <div class="modal-footer">
              <button
                type="button"
                class="btn btn-secondary"
                @click="model == !model"
              >
                بستن
              </button>
              <button
                type="button"
                class="btn btn-primary"
                @click="doedit(form.id)"
              >
                انتشار
              </button>
            </div>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { ref, reactive } from "vue";
import Swal from "sweetalert2";
import { useMeta } from "vue-meta";

export default {
  name: "Post-index",
  setup() {
    useMeta({
      title: process.env.VUE_APP_NAME + " |  مدیریت هتل ها",
      htmlAttrs: { lang: "FA", amp: true },
      description: "مدیریت هتل های ثبت شده در سایت",
    });
    const modal = ref(false);
    const postedit = ref({});
    const editloading = ref(false);
    const loading = ref(false);
    const postloading = ref(false);

    const form = reactive({
      id: "",
      name: "",
      category: "1",
      body: "",
      image: "",
      stars: "1",
      image: "",
      address: "",
      capacity: "1",
      in: "",
      out: "",
      roles: "",
      errorname: "",
      errorcategory: "",
      errorbody: "",
      errorimage: "",
      errorstars: "",
      errorimage: "",
      erroraddress: "",
      errorcapacity: "",
      errorin: "",
      errorout: "",
      errorroles: "",
      imageMessage: "",
    });

    function uploadImage(e) {
      loading.value = true;
      let img = e.target.files[0];
      let fd = new FormData();
      fd.append("image", img);
      axios
        .post(`${process.env.VUE_APP_API_URL}image-upload`, fd)
        .then(function (response) {
          response;
          form.imageMessage = "تصویر با موفقیت آپلود شد";
          form.image = response.data.data.image;
        })
        .catch(function (error) {
          form.imageMessage = error.response.data.message.image;
        });
      if (loading.value == true) {
        form.imageMessage = "درحال آپلود";
      }
    }

    function validate() {
      if (form.name === "") {
        form.errorname = "نام هتل را وارد کنید";
      } else {
        form.errorname = "";
      }
      if (form.image === "") {
        form.imageMessage = "تصویر خود را آپلود کنید";
      } else {
        form.imageMessage = "";
      }
      if (form.body === "") {
        form.errorbody = "متن توضیحات را وارد کنید";
      } else {
        form.errorbody = "";
      }
      if (form.address === "") {
        form.erroraddress = "آدرس هتل را وارد کنید";
      } else {
        form.erroraddress = "";
      }
      if (form.in === "") {
        form.errorin = "ساعت ورود را وارد کنید";
      } else {
        form.errorin = "";
      }
      if (form.out === "") {
        form.errorout = "ساعت خروج را وارد کنید";
      } else {
        form.errorout = "";
      }
      if (form.roles === "") {
        form.errorroles = "قوانین را وارد کنید";
      } else {
        form.errorroles = "";
      }

      if (
        form.name !== "" &&
        form.image !== "" &&
        form.body !== "" &&
        form.address !== "" &&
        form.in !== "" &&
        form.out !== "" &&
        form.roles !== ""
      ) {
        loading.value = true;
        createpost();
      }
    }
    const getCookie = (cookie_name) => {
      const re = new RegExp(`(?<=${cookie_name}=)[^;]*`);
      try {
        return document.cookie.match(re)[0]; // Will raise TypeError if cookie is not found
      } catch {
        return "this-cookie-doesn't-exist";
      }
    };
    let token = getCookie("Token");

    function createpost() {
      axios
        .post(
          `${process.env.VUE_APP_API_URL}create-hotel`,
          {
            name: form.name,
            category: form.category,
            body: form.body,
            image: form.image,
            stars: form.stars,
            image: form.image,
            address: form.address,
            capacity: form.capacity,
            in: form.in,
            out: form.out,
            roles: form.roles,
          },
          {
            headers: {
              Authorization: `Bearer ${token}`,
            },
          }
        )
        .then(function (response) {
          response;
          getposts();
          Swal.fire({
            icon: "success",
            title: "هتل با موفقیت ثبت شد",
            toast: true,
            position: "top-end",
            showConfirmButton: false,
            timer: 4000,
            timerProgressBar: true,
          });
          clear();
        })
        .catch(function (error) {
          Swal.fire({
            icon: "error",
            title: "ارور کد :" + error.response.status,
            toast: true,
            position: "top-end",
            showConfirmButton: false,
            timer: 4000,
            timerProgressBar: true,
          });
        });
    }

    const showpost = ref([]);
    function getposts() {
      postloading.value = true;
      axios
        .get(`${process.env.VUE_APP_API_URL}show-hotels`, {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        })
        .then(function (response) {
          showpost.value = response.data.data.hotels;
          postloading.value = false;
        })
        .catch(function (error) {
          error;
          postloading.value = false;
        });
    }

    function deletepost(id) {
      axios
        .get(`${process.env.VUE_APP_API_URL}delete-hotel/${id}`, {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        })
        .then(function (response) {
          response;
          getposts();
          Swal.fire({
            icon: "success",
            title: "هتل با موفقیت حذف شد",
            toast: true,
            position: "top-end",
            showConfirmButton: false,
            timer: 4000,
            timerProgressBar: true,
          });
        })
        .catch(function (error) {
          console.log(error);
          Swal.fire({
            icon: "error",
            title: "ناتوانی در برقراری ارتباط",
            toast: true,
            position: "top-end",
            showConfirmButton: false,
            timer: 4000,
            timerProgressBar: true,
          });
        });
    }

    function edite(id) {
      modal.value = !modal.value;
      editloading.value = true;
      axios
        .get(`${process.env.VUE_APP_API_URL}show-hotel-by-id/${id}`, {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        })
        .then(function (response) {
          form.id = response.data.data.hotel.id;
          form.name = response.data.data.hotel.h_name;
          form.category = response.data.data.hotel.h_category;
          form.body = response.data.data.hotel.h_body;
          form.image = response.data.data.hotel.h_image;
          form.stars = response.data.data.hotel.h_stars;
          form.image = response.data.data.hotel.h_image;
          form.address = response.data.data.hotel.h_address;
          form.capacity = response.data.data.hotel.h_capacity;
          form.in = response.data.data.hotel.h_in;
          form.out = response.data.data.hotel.h_out;
          form.roles = response.data.data.hotel.h_roles;
          editloading.value = false;
        })
        .catch(function (error) {
          console.log(error);
          editloading.value = false;
          modal.value = false;
          Swal.fire({
            icon: "error",
            title: "ناتوانی در برقراری ارتباط",
            toast: true,
            position: "top-end",
            showConfirmButton: false,
            timer: 4000,
            timerProgressBar: true,
          });
        });
    }

    function doedit(id) {
      axios
        .post(
          `${process.env.VUE_APP_API_URL}edit-hotel/${id}`,
          {
            name: form.name,
            category: form.category,
            body: form.body,
            image: form.image,
            stars: form.stars,
            image: form.image,
            address: form.address,
            capacity: form.capacity,
            in: form.in,
            out: form.out,
            roles: form.roles,
          },
          {
            headers: {
              Authorization: `Bearer ${token}`,
            },
          }
        )
        .then(function (response) {
          response;
          modal.value = !modal.value;
          clear();
          getposts();
          Swal.fire({
            icon: "success",
            title: "هتل با موفقیت ویرایش شد",
            toast: true,
            position: "top-end",
            showConfirmButton: false,
            timer: 4000,
            timerProgressBar: true,
          });
        })
        .catch(function (error) {
          console.log(form);
          Swal.fire({
            icon: "error",
            title: error.message,
            toast: true,
            position: "top-end",
            showConfirmButton: false,
            timer: 4000,
            timerProgressBar: true,
          });
        });
    }

    function clear() {
      (form.name = ""),
        (form.category = ""),
        (form.body = ""),
        (form.image = ""),
        (form.stars = ""),
        (form.image = ""),
        (form.address = ""),
        (form.capacity = ""),
        (form.in = ""),
        (form.out = ""),
        (form.roles = "");
    }

    const categories = ref({});

    function category() {
      axios
        .get(`${process.env.VUE_APP_API_URL}show-categories`, {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        })
        .then(function (response) {
          categories.value = response.data.data.categories;
        })
        .catch(function (error) {
          console.log(error);
        });
    }
    category();

    getposts();
    return {
      form,
      categories,
      uploadImage,
      validate,
      createpost,
      getposts,
      edite,
      deletepost,
      clear,
      doedit,
      editloading,
      postedit,
      modal,
      showpost,
      loading,
      postloading,
    };
  },
};
</script>

<style>
</style>