<template>
  <div class="container auth">
    <div class="row mx-0">
      <div class="col-md-6 px-0 d-none d-sm-block">
        <div class="auth-background"></div>
      </div>
      <div class="col-md-6 px-0">
        <div class="auth-side">
          <h2 class="text-center bold mt-5" style="color: #155263">کدتایید</h2>
          <p class="auth-info mt-4">
            جهت ورود به سایت کد تایید ارسال به ایمیل خود را وارد نمایید
          </p>
          <form class="mt-4" @submit.prevent="validate">
            <input
              class="form-control auth-inputs mt-3"
              placeholder="رمز یکبار مصرف"
              name="otpcode"
              type="number"
              v-model="form.otpcode"
              @input="autovalidate"
            />
            <p class="color-danger text-danger text-center mt-2">
              {{ form.otpcodeErrorText }}
            </p>
            <center>
              <button class="btn auth-submit mt-1" v-if="!loading">
                تایید
              </button>
              <button class="btn auth-submit mt-1" disabled v-else>
                درحال بررسی ...
              </button>
              <router-link
                style="text-decoration: none; color: #155263"
                to="/login"
              >
                <p class="mt-4 bold">تغییر ایمیل</p>
              </router-link>
            </center>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { reactive, ref } from "vue";
import axios from "axios";
import router from "../../router/routes";
import { useRoute } from "vue-router";
import Swal from "sweetalert2";

export default {
  name: "login",
  setup() {
    const route = useRoute();
    const loading = ref(false);

    const form = reactive({
      email: route.query.email,
      otpcode: "",
      otpcodeErrorText: "",
    });

    function autovalidate() {
      if (String(form.otpcode).length > 3) {
        login();
        form.otpcode = !form.otpcode;
      }
    }

    function validate() {
      if (form.otpcode === "") {
        form.otpcodeErrorText = "کد تایید را وارد کنید";
      } else {
        form.otpcodeErrorText = "";
        login();
      }
    }

    function login() {
      loading.value = true;
      axios
        .post(`${process.env.VUE_APP_API_URL}otpconfirm`, {
          email: form.email,
          otpcode: form.otpcode,
        })
        .then(function (response) {
          response;
          loading.value = false;
          let d = new Date();
          d.setTime(d.getTime() + 1 * 24 * 60 * 60 * 1000);
          let expires = "expires=" + d.toUTCString();
          document.cookie =
            "Token=" + response.data.data.token + ";" + expires + ";path=/";
          Swal.fire({
            icon: "success",
            title: "با موفقیت وارد شدید",
            toast: true,
            position: "top",
            showConfirmButton: false,
            timer: 4000,
            timerProgressBar: true,
          });
          router.replace("/");
        })
        .catch(function (error) {
          error;
          loading.value = false;
          Swal.fire({
            icon: "error",
            title: "رمز یکبارمصرف نامعتبر است",
            toast: true,
            position: "top",
            showConfirmButton: false,
            timer: 4000,
            timerProgressBar: true,
          });
        });
    }

    return { validate, autovalidate, form, loading };
  },
};
</script>

