<template>
  <div class="loader" v-if="loading"></div>

  <div class="container mb-5 main-body" v-else>
    <h3 class="fw-bold mb-4" style="color: #155263">
      <i class="bi bi-house-heart-fill" style="color: #ff6f3c"></i> هتل های برتر
    </h3>
    <div class="row">
      <swiper
        :slides-per-view="4"
        :space-between="0"
        :breakpoints="swiperOptions.breakpoints"
        @swiper="onSwiper"
        @slideChange="onSlideChange"
      >
        <swiper-slide v-for="hotel in hotels" :key="hotel.id">
          <router-link
            class="link-none"
            :to="{
              name: 'hotel',
              params: { id: hotel.id, name: hotel.h_name },
            }"
          >
            <div class="col-md-3">
              <div
                class="hotels-card"
                v-bind:style="{
                  backgroundImage: 'url(' + image + hotel.h_image + ')',
                }"
              >
                <div class="hotels-card-title">
                  <h4 class="bold">{{ hotel.h_name }}</h4>
                </div>
                <div class="hotels-card-price">
                  <span
                    class="fa fa-star mt-2"
                    v-bind:class="{ checked: hotel.h_stars > 0 }"
                  ></span>
                  <span
                    class="fa fa-star mt-2"
                    v-bind:class="{ checked: hotel.h_stars > 1 }"
                  ></span>
                  <span
                    class="fa fa-star mt-2"
                    v-bind:class="{ checked: hotel.h_stars > 2 }"
                  ></span>
                  <span
                    class="fa fa-star mt-2"
                    v-bind:class="{ checked: hotel.h_stars > 3 }"
                  ></span>
                  <span
                    class="fa fa-star mt-2"
                    v-bind:class="{ checked: hotel.h_stars > 4 }"
                  ></span>
                </div>
              </div>
            </div>
          </router-link>
        </swiper-slide>
      </swiper>
    </div>
  </div>

  <div class="city_hotel_break d-none d-sm-block">
    <span class="airplane"></span>
  </div>
</template>

<script>
import { Swiper, SwiperSlide } from "swiper/vue";
import { ref } from "vue";
import "swiper/css/bundle";
import "swiper/css/autoplay";
import axios from "axios";
export default {
  name: "main",
  components: {
    Swiper,
    SwiperSlide,
  },
  setup() {
    const image = process.env.VUE_APP_IMAGES_URL;
    const hotels = ref([]);
    const loading = ref(false);
    function getposts() {
      loading.value = true;
      axios
        .get(`${process.env.VUE_APP_API_URL}show-hotels`)
        .then(function (response) {
          hotels.value = response.data.data.hotels;
          loading.value = false;
        })
        .catch(function (error) {
          error;
        });
    }
    getposts();
    return { hotels, loading, image };
  },
  data() {
    return {
      swiperOptions: {
        breakpoints: {
          320: {
            slidesPerView: 1,
            spaceBetween: 0,
          },
          770: {
            slidesPerView: 2,
            spaceBetween: 50,
          },
          992: {
            slidesPerView: 3,
            spaceBetween: 30,
          },
          1400: {
            slidesPerView: 4,
            spaceBetween: 30,
          },
        },
      },
    };
  },
};
</script>

